<template>
    <section>
        <div class="col-lg-12 mb-2 mt-2 p-0" id="week-stat">
            <div class="expand-list">
                <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                    <i class="icon-solid arrow-solid-left" style="font-size:9pt"></i>
                </a>
            </div>
            <div class="border eqcol h-100 pt-0 px-3 pb-3">
                <div class="updatefull-widg">
                    <div class="row border-bottom bg-f5f5f5 mb-2">
                        <div class="col-md-6">
                            <h6 class="py-3 mb-0">
                                {{ $t('Raised') + ' ' + $t('By') }}:
                                <template v-if="TicketData.createdBy == GetUserInfo.ID && UserType == 'CL'">
                                    <span class="blue-time">{{ TicketData.raisedBy }}</span>{{ $t('You') }}
                                </template>
                                <template v-else>
                                    <span class="blue-time">{{ TicketData.raisedBy }}</span>
                                    <template v-if="TicketData.createdUserTypeId == 4">
                                        {{ $t('Client') }}
                                    </template>
                                    <template v-else>
                                        ({{ TicketData.departmentName.DEPT_NAME }})
                                    </template>
                                </template>
                            </h6>
                        </div>
                        <div class="col-md-6 text-right">
                            <h6 class="py-3 mb-0">{{ $t('AssignedTo') }}:
                                <span class="blue-time">{{ TicketData.assignedTo == null ? $t('NotAssigned') :
                                    TicketData.assignedTo }} </span>
                                ({{ isBlank(TicketData.ticketDepartmentId) == true ? '' : TicketData.ticketDepartmentId }})
                            </h6>
                        </div>
                    </div>
                    <div class="row pt-1 px-3">
                        <div class="col-lg-6 p-0">
                            <h2 class="time_info">
                                {{ TicketData.ticketSubject }}
                                <span :class="getStatusClass(TicketData.status, 'status_name', 'fsm')"
                                    style="font-size: 16px;display: inline !important;">
                                    {{ TicketData.status }}
                                </span>
                            </h2>
                            <p class="float-left col-sm-12 col-lg-6 p-0"><b>{{ $t('Category') }}:</b>
                                {{ $t(TicketData.ticketCategoryName) == '' ? '--' : $t(TicketData.ticketCategoryName) }}
                            </p>
                            <p class="float-left col-sm-12 col-lg-6 p-0">
                                <b>{{ $t('Priority') }}: </b>
                                <span v-if="TicketData.priority == 'Critical'"
                                    class="Timer d-inline text-white critical-color" style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                                <span v-else-if="TicketData.priority == 'Medium'"
                                    class="Timer d-inline text-white noti-blue" style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                                <span v-else-if="TicketData.priority == 'High' || TicketData.priority == 'Highest'"
                                    class="Timer d-inline text-white noti-orange" style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                                <span v-else-if="TicketData.priority == 'Low'" class="Timer d-inline text-white noti-green"
                                    style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                                <span v-else class="Timer d-inline text-white noti-blue" style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                            </p>
                            <p class="mb-1 float-left col-sm-12 col-lg-6 p-0"><b>{{ $t('OpenDate') }}:
                                </b>{{ $options.filters.formatDateTime(TicketData.createdAt) }}</p>
                            <p class="float-left col-sm-12 col-lg-6 p-0"><b>{{ $t('Expected_Resolution') }}: </b>
                                {{ TicketData.expectedResolutionDate == null ? '--' :
                                    $options.filters.formatDate(TicketData.expectedResolutionDate) }}
                            </p>
                        </div>
                        <div class="col-lg-6 text-lg-right p-0"
                            v-if="ShowSLATime && TicketData.TicketSlaId > 0 && UserType != 'CL' && (isTicketResolve == 'yes' || isTicketHOD == 'yes' || UserType == 'CA' || UserType == 'SA')">
                            <div class="timer_respbase tickettimershowarea">
                                <div class="row">
                                    <div class="float-right col-sm-12 col-lg-12 p-2">
                                        <b>Response Time:</b> <span class="btn Timer d-inline-block text-white bg-danger"
                                            style="cursor:default;width:110px;">
                                            <span class="timer-response px-1 text-center tickettimer" data-toggle="tooltip"
                                                data-placement="top" title=""><a class="digital-timers text-white"
                                                    target="_blank" style="text-decoration: none; font-weight: 600;">{{
                                                        ResponseTime }}</a></span>
                                        </span>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="float-right col-sm-12 col-lg-12 p-2">
                                        <b>Resolve Time:</b> <span class="btn Timer d-inline-block text-white bg-success"
                                            style="cursor:default;width:110px;">
                                            <span class="timer-resolve px-1 text-center tickettimer" data-toggle="tooltip"
                                                data-placement="top" title=""><a class="digital-timers text-white"
                                                    target="_blank" style="text-decoration: none; font-weight: 600;">{{
                                                        ResolveTime }}</a></span>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="mb-1 col-12 p-0">
                        <b v-if="ListOfCCUsers === ''">{{ $t('CCuppercase') }}: --</b>
                        <b v-if="ListOfCCUsers !== ''">{{ $t('CCuppercase') }}: {{ ListOfCCUsers }}</b>
                    </div>
                    <div class="ticketShortDesciption mb-2 mt-2">
                        <div class="addednote-ticket-content">
                            <div class="col-12 p-0" id="divlessdata">
                                <div class="readmore textelipsis_inner">
                                    <div>
                                        <b> {{ $t('Description') }}: </b>
                                        <span v-html="TruncateStr(TicketData.description, 185)"></span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-12 p-0" id="divshowmoredata" style="display:none;pointer-events:none;">
                                <div class="readmore">
                                    <div>
                                        <b> {{ $t('Description') }}: </b>
                                        <span v-html="TicketData.description"></span>
                                    </div>
                                </div>
                            </div>

                            <!-- && TicketData.description.length > 185 -->
                            <a v-if="isBlank(TicketData.parseTableId)"
                                href="javascript:;" v-on:click="showTicketDetail" class="lessmoredesc ancreadmoredesc">{{
                                    $t('readmore') }}</a>
                            <a v-if="!isBlank(TicketData.parseTableId)"
                                href="javascript:;" v-on:click="openTicketTextTab" class="lessmoredesc ancreadmoredesc">{{
                                    $t('readmore') }}</a>
                            <div class="addednote-ticket-content readmoredesc" style="display:none;">
                                <a href="javascript:;" class="anclessmoredesc"> {{ $t('Read') + ' ' + $t('Less') }} </a>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- //Usic Project communication Section hide As per Discusion with hardeep Sir Dated 29-junue 2023
            @if (TALYGEN.StatusClass.CheckExtraFeatures("ACCOUNTSETUP", loggedUserInfo.CompanyId))
            {

            }
            else
            {
                if (Model.OnBehalfConsumerId == null && !Model.IsSupportTicket)
                { -->
                <div class="communication"></div>
                <div class="row">
                    <div class="col-md-12 d-flex">
                        <p class="mr-4">{{ $t('Call') }}: <a @click="OpenChat" id="call" title="Call" class="voicecall">
                                <i class="fa fa-phone call-color"></i></a></p>
                        <p class="mr-4">{{ $t('VideoCall') }}: <a @click="OpenChat" id="videocall" title="Video Call"
                                class="videocall"> <i class="fa fa-video-camera video-color"></i></a></p>
                        <p class="mr-4">{{ $t('SMS') }}: <a @click="OpenChat" id="sms" title="Send SMS" class="sendsms"><i
                                    class="fa fa-commenting sms-color"></i></a></p>
                        <p class="mr-4">{{ $t('Chat') }}: <a @click="OpenChat" id="chat" title="Chat" class="chat"><i
                                    class="fa fa-comments chat-color"></i></a></p>
                    </div>
                </div>
                <!-- }
            } -->
            </div>
        </div>

        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isMoreTicketDetail">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title" v-if="isTicketDetail">{{ $t('TicketDetails') }}</h4>
                                <h4 class="modal-title" v-if="isTicketText">{{ $t('TicketText') }}</h4>
                                <button type="button" class="close" v-on:click="Close">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">
                                <div class="cardv-body float-left pt-0 border col-12 float-left p-1">
                                    <div class="card-text evemgmt_details">
                                        <div v-if="isTicketDetail"
                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left panel1 mb-0 p-0"
                                            style="min-height:100px;max-height: 500px; overflow-x: hidden;">
                                            <template v-for="item in LeftViewData">
                                                <p class="px-2 w-50 float-left"
                                                    v-if="item.form_field_visibility == 'YES' && item.group_name !== 'Attachment' && item.group_name !== 'Recurring'">
                                                    <i class="fa fa-dot-circle-o text-dark float-left pr-2"
                                                        style="font-size:12pt;"></i>
                                                    <span class="pl-1 font-weight-bold">{{ GetLocalizedValue(item.name,
                                                        item.display_name) }}: </span>
                                                    <span v-html="item.value"
                                                        v-if="item.name != 'status_id' && item.value != null"></span>
                                                    <span
                                                        v-else-if="item.name != 'cc_users' && item.value == null">--</span>
                                                    <span v-if="item.name == 'status_id'"
                                                        :class="{ 'bg-secondary text-center px-2 text-white': true, 'bg-success': item.value == 'Open', 'bg-danger': (item.value == 'Closed' || item.value == 'Cancelled' || item.value == 'Closed and Locked'), 'bg-warning': item.value == 'Draft' }">{{
                                                            item.value }}</span>
                                                    <span v-if="item.name == 'cc_users'">{{
                                                        ListOfCCUsers }}</span>
                                                </p>
                                            </template>

                                        </div>
                                        <div v-if="isTicketText"
                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left panel1 mb-0 p-0"
                                            style="min-height:100px;max-height: 500px; overflow-x: hidden;">
                                            <pre v-html="TicketTextDetail" v-if="!isBlank(TicketTextDetail)"></pre>
                                            <div class="col-12 text-center text-danger" v-if="!isBlank(TicketTextDetail)">
                                                {{ $t('NoRecordfound') }}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        TicketId: {
            type: Number,
            required: false
        },
        TicketData: {
            type: Object,
            required: true,
        },
        TicketDetailDataForPopup: {
            type: Array,
            required: true,
        },
        ListOfCCUsers: {
            type: String,
            required: true
        },
        ShowSLATime: {
            type: Boolean,
            required: true
        },
        isTicketResolve: {
            type: Boolean,
            required: true
        },
        isTicketHOD: {
            type: Boolean,
            required: true
        }
    },
    data() {
        return {
            UserType: null,
            isMoreTicketDetail: false,
            TicketTextDetail: '',
            isTicketText: false,
            isTicketDetail: false,
            LeftViewData: [],
            isLoading: false,
            ResponseTime: 0,
            ResolveTime: 0,
            timeZone: ''
        }

    },
    created() {
        var vm = this;
        //debugger;
        vm.UserType = vm.GetUserInfo.UserType;
        vm.CalculateSlATime(vm.TicketData.response_time, vm.TicketData.resolve_time);
        if (vm.TicketData.status == "TICKET_CLOSEDLOCKED" || vm.TicketData.status == "TICKET_CLOSED") {
            vm.ShowSLATime = false;
        }
    },
    methods: {
        CalculateSlATime: function (respTime, resTime) {
            var vm = this;
            if (respTime != null && resTime != null) {
                var ticketResponseRemainingTime = new Date(respTime);
                var ticketResolveRemainingTime = new Date(resTime);

                var responseTime = 0;
                var resolveTime = 0;
                if (ticketResponseRemainingTime) {
                    var currentTime = new Date();
                    var diff = ticketResponseRemainingTime - currentTime;
                    responseTime = Math.floor(diff / (1000 * 60)); // Convert milliseconds to minutes
                }

                if (ticketResolveRemainingTime) {
                    var currentTime = new Date();
                    var diff = ticketResolveRemainingTime - currentTime;
                    resolveTime = Math.floor(diff / (1000 * 60)); // Convert milliseconds to minutes
                }
                setTimeout(function () {
                    vm.Timer(responseTime, resolveTime);
                }, 1000);
            }
        },
        Timer: function (ResponseTime, ResolveTime) {
            //debugger;
            var vueObj = this;
            var responseTime = ResponseTime;
            var resolveTime = ResolveTime;
            var showResponse = "1";
            if (responseTime == undefined || responseTime == "0") {
                showResponse = "0";
            }
            var showResolve = "1";
            if (resolveTime == undefined || resolveTime == "0") {
                showResolve = "0";
            }
            var showSLA = "yes";

            // Call StartTimer function with appropriate parameters
            vueObj.StartTimer(responseTime, resolveTime, showResponse, showResolve, showSLA);


        },
        StartTimer: function (responseTime, resolveTime, showResponse, showResolve, SLA_Value) {
            //debugger;
            var vueObj = this;
            var durationResponseTime = 0;
            var durationResolveTime = 0; // Declare durationResolveTime
            if (showResponse == "1") {
                durationResponseTime = responseTime;
            }
            if (showResolve == "1") {
                durationResolveTime = resolveTime;
            }

            if (showResponse == "1" || showResolve == "1") {
                var negativeResponseTime = false;
                var negativeResolveTime = false;
                var timerResponseTime = durationResponseTime;
                var timerResolveTime = durationResolveTime;
                setInterval(function () {
                    if (SLA_Value.toLowerCase() == "no") {
                        vueObj.ResponseTime = "--";
                        vueObj.ResolveTime = "--";
                    } else {
                        // Time calculations for ResponseTime
                        if (timerResponseTime != undefined && responseTime != "0" && responseTime != "") {
                            var mins_num = parseFloat(timerResponseTime, 10);
                            var hours = Math.floor(mins_num / 60);
                            var minutes = Math.floor((mins_num - (hours * 3600)) / 60);
                            var seconds = Math.floor(mins_num - (hours * 3600) - (minutes * 60));
                            hours = hours < 10 ? "0" + hours : hours;
                            minutes = minutes < 10 ? "0" + minutes : minutes;
                            seconds = seconds < 10 ? "0" + seconds : seconds;
                            if (negativeResponseTime) {
                                vueObj.ResponseTime = "-" + hours + ":" + minutes + ":" + seconds;
                                timerResponseTime = timerResponseTime + 1 / 60;
                            } else {
                                vueObj.ResponseTime = hours + ":" + minutes + ":" + seconds;
                                timerResponseTime = timerResponseTime - 1 / 60;
                                negativeResponseTime = false;
                                if (timerResponseTime < 0) {
                                    negativeResponseTime = true;
                                }
                            }
                        } else {
                            vueObj.ResponseTime = "00:00:00";
                        }

                        // Time calculations for ResolveTime
                        if (timerResolveTime != undefined && resolveTime != "0" && resolveTime != "") {
                            var mins_num_resolve_time = parseFloat(timerResolveTime, 10);
                            var hours_resolve_time = Math.floor(mins_num_resolve_time / 60);
                            var minutes_resolve_time = Math.floor((mins_num_resolve_time - (hours_resolve_time * 3600)) / 60);
                            var seconds_resolve_time = Math.floor(mins_num_resolve_time - (hours_resolve_time * 3600) - (minutes_resolve_time * 60));
                            hours_resolve_time = hours_resolve_time < 10 ? "0" + hours_resolve_time : hours_resolve_time;
                            minutes_resolve_time = minutes_resolve_time < 10 ? "0" + minutes_resolve_time : minutes_resolve_time;
                            seconds_resolve_time = seconds_resolve_time < 10 ? "0" + seconds_resolve_time : seconds_resolve_time;
                            if (negativeResolveTime) {
                                vueObj.ResolveTime = "-" + hours_resolve_time + ":" + minutes_resolve_time + ":" + seconds_resolve_time;
                                timerResolveTime = timerResolveTime + 1 / 60;
                            } else {
                                vueObj.ResolveTime = hours_resolve_time + ":" + minutes_resolve_time + ":" + seconds_resolve_time;
                                timerResolveTime = timerResolveTime - 1 / 60;
                                negativeResolveTime = false;
                                if (timerResolveTime < 0) {
                                    negativeResolveTime = true;
                                }
                            }
                        } else {
                            vueObj.ResolveTime = "00:00:00";
                        }
                    }
                }, 1000);
            } else {
                vueObj.ResponseTime = "--";
                vueObj.ResolveTime = "--";
            }
        },
        Close: function () {
            var vm = this;
            vm.isMoreTicketDetail = false;
        },
        showTicketDetail: function () {
            var vm = this;
            vm.isMoreTicketDetail = true;
            vm.isTicketDetail = true;
            vm.isTicketText = false;
            vm.LeftViewData = vm.TicketDetailDataForPopup;

        },
        OpenChat: function () {
            debugger
            this.OpenWindow('https://app.talygen.com/Communication/Index?reqhFrom=40348', 800, 1200, false);
        },
        OpenWindow: function (query, h, w, scroll) {
            var isMobile;
            var chatlogwindow;
            if (isMobile == true) {
                localStorage.removeItem('clientOpen');
            }

            var screenWidth = window.innerWidth * 0.7;
            var screenHeight = window.innerHeight * 0.8;

            var l = (window.innerWidth - screenWidth) / 2;
            var t = (window.innerHeight - screenHeight) / 3;

            var winprops = 'height=' + screenHeight + ',width=' + screenWidth + ',top=' + t + ',resizable=0,status=no,menubar=no,toolbar=no,location=no,addressbar=no,left=' + l;
            if (scroll) winprops += ',scrollbars=1';

            var stateClient = localStorage.getItem('clientOpen'); //window.localStorage.getItem

            if (stateClient == "1") {
                console.log(" localStorage.removeItem('clientOpen');");
                if (window.navigator.userAgent.indexOf("Edge") > -1) // because edge does not have local object global
                {
                    chatlogwindow = window.open(query, "_blank", winprops);
                }
            } else {
                if (isMobile == true) {
                    chatlogwindow = window.open(query, "_self", winprops);
                } else {
                    //   localStorage.setItem('clientOpen', '1');
                    chatlogwindow = window.open(query, "_blank", winprops);
                }
            }
        },
        openTicketTextTab: function () {
            var vm = this;
            //debugger;
            vm.isMoreTicketDetail = true;
            vm.isTicketText = true;
            vm.isTicketDetail = false;
            var url = `TicketId=${this.TicketId}&formId=`;
            DataService.GetTicketText(url).then(function (response) {
                //debugger;
                if (response.data != null && response.data.status == "Success") {
                    vm.TicketTextDetail = response.data.result.DATA[0].TICKETTEXT;
                }
            });
        },

    }
}
</script>